import React from 'react'
import styled from 'styled-components'
import {string, number, bool} from 'prop-types'

const PropTypes = {
  className: string,
  big: bool,
  centered: bool,
  temperature: number.isRequired,
  weather: string.isRequired
};

const DefaultProps = {
  big: false,
  centered: false
};

const Display = styled.div`
  display: flex;
  flex-direction: column;
  text-align: center;
`;

const Temperature = styled.h2`
  font-size: ${props => props.big ? "3em" : "1.5em"};
`;

const Weather = styled.p`
  font-size: ${props => props.big ? "1.5em" : "0.75em"};
  text-align: ${props => props.centered ? "center" : "end"};
`;

const TempDisplay = props => {
  return (
    <Display className={props.className}>
      <Temperature big={props.big}>{props.temperature}Â°C</Temperature>
      <Weather big={props.big} centered={props.centered}>{props.weather}</Weather>
    </Display>
  )
};

TempDisplay.propTypes = PropTypes;
TempDisplay.defaultProps = DefaultProps;

export default TempDisplay;