import React from 'react'
import styled from 'styled-components'
import {string, number} from 'prop-types'

const PropTypes = {
  className: string,
  humidity: number.isRequired,
  windSpeed: number.isRequired,
  pressure: number.isRequired
};

const WeatherInfoBody = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-around;
`;

const WeatherInfo = props => {
  const { humidity, windSpeed, pressure } = props;
  return (
    <WeatherInfoBody className={props.className}>
      <p>Humidity: {humidity}%</p>
      <p>Wind: {windSpeed}m/s</p>
      <p>Pressure: {pressure}mm</p>
    </WeatherInfoBody>
  );
};

WeatherInfo.propTypes = PropTypes;

export default WeatherInfo;