import React from 'react'
import styled from 'styled-components'
import {string, arrayOf, array} from 'prop-types'

import SideMenu from "./SideMenu";
import ForecastDay from "./ForecastDay";

const PropTypes = {
  className: string,
  forecast: arrayOf(array)
};

const DefaultProps = {
  forecast: [[]]
};

const Layout = styled.div`
  flex: 1;
  
  display: grid;
  grid-auto-flow: column;
  grid-gap: 15px;
  
  @media (max-width: 899px) {
    grid-auto-flow: row;
    grid-gap: 5px;
  }
`;

const WeatherSideMenu = props => {
  return (
    <SideMenu className={props.className}>
      <Layout>
        {props.forecast.map(day => <ForecastDay day={day} key={day[0].date}/>)}
      </Layout>
    </SideMenu>
  );
};

WeatherSideMenu.propTypes = PropTypes;
WeatherSideMenu.defaultProps = DefaultProps;

export default WeatherSideMenu;