const currentWeatherEndpoint = "https://api.openweathermap.org/data/2.5/weather";
const forecastWeatherEndpoint = "https://api.openweathermap.org/data/2.5/forecast";
const API_KEY = 'db9072c49ae714096027e364cc2f3d97';

async function getCurrentWeather(city) {
    const currentWeatherCall = await fetch(`${currentWeatherEndpoint}?q=${city}&appid=${API_KEY}&units=metric`);
    return await currentWeatherCall.json();
}

export async function getForecastWeather(city) {
    const forecastCall = await fetch(`${forecastWeatherEndpoint}?q=${city}&appid=${API_KEY}&units=metric`);
    return await forecastCall.json();
}

export default getCurrentWeather;