import React from 'react'
import styled from 'styled-components'
import {string, number, array, bool, func} from 'prop-types'
import WeatherSideMenu from "./components/WeatherSideMenu";
import WeatherCard from "./components/WeatherCard";

const PropTypes = {
  className: string,
  cityName: string.isRequired,
  temperature: number.isRequired,
  weather: string.isRequired,
  humidity: number.isRequired,
  windSpeed: number.isRequired,
  pressure: number.isRequired,
  sunrise: number.isRequired,
  sunset: number.isRequired,
  forecast: array,
  remove: func.isRequired,
  toggle: func.isRequired,
  open: bool
};

const DefaultProps = {
  forecast: [],
  open: false
};

const Container = styled.div`
  position: relative;
  z-index: 1;
  display: grid;
`;

const StyledSideMenu = styled(WeatherSideMenu)`
  position: relative;
  z-index: 3;
  margin: 20px 20px 20px -20px;
  border-radius: 0 5px 5px 0;
  
  @media (max-width: 899px) {
    margin: -20px 20px 20px 20px;
    border-radius: 0 0 5px 5px;
  }
`;

const StyledCard = styled(WeatherCard)`
  position: relative;
  z-index: 5;
  margin: 20px;
`;

const WeatherCardWithSideMenu = props => {
  const { cityName, temperature, weather, humidity, windSpeed, pressure, sunrise, sunset, forecast } = props;

  return (
    <Container open={props.open} className={props.className}>
      <StyledCard cityName={cityName}
                   temperature={temperature}
                   weather={weather}
                   humidity={humidity}
                   windSpeed={windSpeed}
                   pressure={pressure}
                   sunrise={sunrise}
                   sunset={sunset}
                   remove={props.remove}
                   toggle={props.toggle}/>
      {props.open && <StyledSideMenu forecast={forecast}/>}
    </Container>
  );
};

WeatherCardWithSideMenu.propTypes = PropTypes;
WeatherCardWithSideMenu.defaultProps = DefaultProps;

export default WeatherCardWithSideMenu;