import React from 'react'
import styled from 'styled-components'
import {string, number} from 'prop-types'

const PropTypes = {
  className: string,
  sunrise: number.isRequired,
  sunset: number.isRequired
};

const timeOptions = {hour: '2-digit', minute: '2-digit'};

const Container = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
`;

const DayCycle = props => {
  const sunrise = new Date(props.sunrise).toLocaleString('ru', timeOptions);
  const sunset = new Date(props.sunset).toLocaleString('ru', timeOptions);

  return (
    <Container className={props.className}>
      <p>{sunrise}</p>
      <p>{sunset}</p>
    </Container>
  );
};

DayCycle.propTypes = PropTypes;

export default DayCycle;