import React from 'react'
import styled from 'styled-components'
import {string, func} from 'prop-types'

const PropTypes = {
  className: string,
  color: string,
  pickTheme: func
};

const DefaultProps = {
  color: "#555",
  pickTheme: () => {}
};

const Button = styled.button`
  position: relative;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 0;
  background: radial-gradient(farthest-side at 35% 35%, rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0.1)),
              ${props => props.color};
  
  &:focus {
    outline: none;
  }
`;

const ShinyButton = ({ className, color, pickTheme}) => {
  return (
    <Button className={className}
            color={color}
            onClick={pickTheme}/>
  );
};

ShinyButton.propTypes = PropTypes;
ShinyButton.defaultProps = DefaultProps;

export default ShinyButton;