import React from 'react'
import styled from 'styled-components'
import {string, arrayOf, node, number} from 'prop-types'

const PropTypes = {
  className: string,
  rows: arrayOf(number),
  columns: arrayOf(number),
  children: node
};

const DefaultProps = {
  rows: [1],
  columns: [1]
};

const Layout = styled.div`
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-rows: ${props => props.rows ? props.rows.join('fr ') + 'fr' : '1fr'};
  grid-template-columns: ${props => props.columns ? props.columns.join('fr ') + 'fr' : '1fr'};
`;

const GridLayout = props => {
  return (
    <Layout rows={props.rows} columns={props.columns}>{props.children}</Layout>
  );
};

GridLayout.propTypes = PropTypes;
GridLayout.defaultProps = DefaultProps;

export default GridLayout;