import React from 'react'
import styled from 'styled-components'
import {string, number, func} from "prop-types"

import Card from "./Card";
import GridLayout from "../../../../GridLayout";
import Title from "./Title";
import TempDisplay from "./TempDisplay";
import DayCycle from "./DayCycle";
import ToggleButton from "./ToggleButton";
import CrossButton from "./CrossButton";
import WeatherInfo from "./WeatherInfo";

const PropTypes = {
  className: string,
  cityName: string.isRequired,
  temperature: number.isRequired,
  weather: string.isRequired,
  humidity: number.isRequired,
  windSpeed: number.isRequired,
  pressure: number.isRequired,
  sunrise: number.isRequired,
  sunset: number.isRequired,
  remove: func.isRequired,
  toggle: func.isRequired
};

const StyledToggle = styled(ToggleButton)`
  margin: 5px 15px;
`;

const StyledCrossButton = styled(CrossButton)`
  position: absolute;
  top: 5px;
  right: 5px;
`;

const StyledTempDisplay = styled(TempDisplay)`
  align-self: center;
  
  @media (max-width: 599px) {
    font-size: 12px;
  }
`;

const WeatherCard = props => {
  const { cityName, temperature, weather, humidity, windSpeed, pressure, sunrise, sunset } = props;
  return (
      <Card className={props.className}>
        <StyledCrossButton onClick={props.remove}/>
        <GridLayout rows={[1, 2, 3, 2, 1]}>
          <Title>{cityName}</Title>
          <StyledTempDisplay temperature={temperature}
                       weather={weather}
                       centered big/>
          <WeatherInfo humidity={humidity}
                       windSpeed={windSpeed}
                       pressure={pressure}/>
          <DayCycle sunrise={sunrise} sunset={sunset}/>
          <StyledToggle text={"Forecast"} onClick={props.toggle}/>
        </GridLayout>
      </Card>
  );
};

WeatherCard.propTypes = PropTypes;

export default WeatherCard;