const API_KEY = "AtZXuLgTcqINybvNxDwkEi2zQZ-ZT07_34zj8AYJTWBydl8hmNOqSLn2b6qI7Y-a";
const TimeZoneEndpoint = "https://dev.virtualearth.net/REST/v1/TimeZone/";

//returns offset in seconds
async function getTimeZoneOffset(lat, lon)  {
    const timeZone_api = await fetch(`${TimeZoneEndpoint}${lat},${lon}?key=${API_KEY}`);

    if (timeZone_api.status === 200) {

      const timeZoneResponse = await timeZone_api.json();
      const timeZoneOffset = timeZoneResponse.resourceSets[0].resources[0].timeZone.convertedTime.utcOffsetWithDst;
      return +timeZoneOffset.slice(0, -3) * 3600;

    } else {
      throw new Error(`${timeZone_api.status}, ${timeZone_api.statusText}`);
    }
}

export async function getDifferenceWithCurrentZone(lat, lon)  {
    const timeOffset = await getTimeZoneOffset(lat, lon);
    const localTimeZoneOffset = new Date().getTimezoneOffset() * 60;
    return timeOffset + localTimeZoneOffset;
}


export default getTimeZoneOffset;