import React from 'react'
import styled from 'styled-components'
import {string, object, func} from 'prop-types'

import ShinyButton from "./components/ShinyButton";

const PropTypes = {
  className: string,
  themes: object,
  selectTheme: func
};

const DefaultProps = {
  themes: [{
    placeholder: "#555"
  }],
  selectTheme: () => {}
};

const Container = styled.div`
  display: flex;
  justify-content: space-around;
  align-items: center;
`;

const validColors = /(#[0-9abcdef]{3,6})|(rgba?\(.+\))|(hsla?\(.+\))/i;

const ThemeSelector = ({ className, themes, selectTheme }) => {

  const buttons = Object.keys(themes).map(theme => {
    const currTheme = themes[theme];

    let buttonColor = Object.keys(currTheme).map(themeColor => {
      const color = currTheme[themeColor].match(validColors);
      return color ? color[0] : "";
    });
    buttonColor = buttonColor.length === 1 ? buttonColor[0] : `linear-gradient(135deg, ${buttonColor.join(",")})`;

    return <ShinyButton color={buttonColor}
                        pickTheme={() => selectTheme(themes[theme])}
                        key={theme} />
  });

  return (
    <Container className={className}>
      {buttons}
    </Container>
  );
};

ThemeSelector.propTypes = PropTypes;
ThemeSelector.defaultProps = DefaultProps;

export default ThemeSelector;