import React from 'react'
import styled from 'styled-components'
import {string, func} from 'prop-types'

const PropTypes = {
  className: string,
  text: string.isRequired,
  placeholder: string,
  onSubmit: func.isRequired
};

const DefaultProps = {
  text: "Add",
  placeholder: "",
  onSubmit: () => {}
};

const Form = styled.form`
  flex: 1;
  display: grid;
  grid-template-columns: 3fr 1fr;
  grid-column-gap: 10px;
`;

const Input = styled.input`
  padding: 0 3px;
  border: 2px solid ${props => props.theme.additional || "orangered"};
  border-radius: 5px;
  background: transparent;
  color: white;
  
  &:focus {
    outline: none;
  }
`;

const Button = styled.button`
  padding: 2px 4px;
  border: 0;
  border-radius: 5px;
  box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.4);
  background: ${props => props.theme.additional || "orangered"};
  
  &:active {
    transform: translate(1px, 1px);
    box-shadow: none;
  }
  
  &:focus {
    outline: none;
  }
`;

class InputForm extends React.Component {
  state = {
    value: ''
  };

  handleChange = event => {
    this.setState({
      value: event.target.value
    });
  };

  handleSubmit = event => {
    event.preventDefault();
    this.props.onSubmit(this.state.value);

    this.setState({
      value: ''
    });
  };

  render() {
    const { placeholder, text, className } = this.props;

    return (
      <Form onSubmit={this.handleSubmit} className={className}>
        <Input type={"text"}
               value={this.state.value}
               onChange={this.handleChange}
               placeholder={placeholder}/>
        <Button type={"Submit"}>{text}</Button>
      </Form>
    )
  }
}

InputForm.propTypes = PropTypes;
InputForm.defaultProps = DefaultProps;

export default InputForm