import React from 'react'
import styled from 'styled-components'
import {string, func} from "prop-types"

const PropTypes = {
  className: string,
  onClick: func.isRequired
};

const Cross = styled.button`
  position: relative;
  width: 20px;
  height: 20px;
  background: transparent;
  border: none;
  
  &:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 1px;
    top: calc(50% - 0.5px);
    left: 0;
    transform:  rotate(-45deg);
    background: black;
  }
  
  &:after {
    content: '';
    position: absolute;
    width: 1px;
    height: 100%;
    top: 0;
    left: calc(50% - 0.5px);
    transform:  rotate(-45deg);
    background: black;
  }
  
  &:focus {
    outline: none;
  }
  
  &:active {
    transform: translate(1px, 1px);
  }
`;

const CrossButton = props => {
  return (
    <Cross className={props.className} onClick={props.onClick} />
  );
};

CrossButton.propTypes = PropTypes;

export default CrossButton;