const API_KEY = "trnsl.1.1.20190408T131030Z.aa191d583b07949e.33697662ad8fa451f72fb8c8a81952e676a4667f";
const API_ENDPOINT = "https://translate.yandex.net/api/v1.5/tr.json/translate";

async function getTranslatedText(text) {
  const encodedText = encodeURIComponent(text);

  if (encodedText === text) return text;


  const response = await fetch(`${API_ENDPOINT}?key=${API_KEY}&text=${encodedText}&lang=en`);
  if (response.status !== 200) throw new Error(`${response.status}, ${response.statusText}`);

  const jsonResponse = await response.json();
  return jsonResponse.text[0];
}

export default getTranslatedText;