import React from 'react'
import styled from 'styled-components'
import {string, array} from 'prop-types'

import TimeOfDayWeather from "./TimeOfDayWeather";

const PropTypes = {
  className: string,
  day: array.isRequired
};

const DefaultProps = {
  day: [{
    day: "Monday",
    date: "01.01"
  }]
};

const Day = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`;

const WeekDay = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const DayName = styled.h2`
  @media (max-width: 599px) {
    font-size: 16px;
  }
`;

const ForecastDay = ({ day }) => {
  return (
    <Day>
      <WeekDay>
        <DayName>{day[0].day}</DayName>
        <p>{day[0].date}</p>
      </WeekDay>
      {day.map(e => <TimeOfDayWeather {...e} key={e.timeOfDay}/>)}
    </Day>
  );
};

ForecastDay.propTypes = PropTypes;
ForecastDay.defaultProps = DefaultProps;

export default ForecastDay;