import React from 'react'
import styled from 'styled-components'
import { createGlobalStyle, ThemeProvider} from 'styled-components'

import getCurrentWeather, {getForecastWeather} from "./Services/Weather/weatherAPICalls";
import {getDifferenceWithCurrentZone} from "./Services/TimeZone/timeZoneCalls";
import themes from './themes'
import WeatherWidget from "./components/WeatherWidget/WeatherWidget";
import ErrorPopup from './components/Popups/ErrorPopup'
import WarningPopup from './components/Popups/WarningPopup'
import getTranslatedText from "./Services/Translation/YaTranslate";

function setValuesToObjectProps(properties, value, object) {
  properties = properties.slice();
  while (properties.length) object[properties.pop()] = value;
}

const days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
const timesOfDay = {};
setValuesToObjectProps([6, 7, 8], 'Morning', timesOfDay);
setValuesToObjectProps([12, 13, 14], 'Afternoon', timesOfDay);
setValuesToObjectProps([21, 22, 23], 'Evening', timesOfDay);
setValuesToObjectProps([0, 1, 2], 'Night', timesOfDay);

const dateOptions = {month: '2-digit', day: '2-digit'};

const GlobalStyles = createGlobalStyle`
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
`;

const Container = styled.div`
  min-height: 100vh;
  padding: 5vh 0;
  display: flex;
  justify-content: center;
  align-items: center;
  
  background: ${props => props.theme.bgColor || "linear-gradient(45deg, #7349e5 0%,#ad1283 76%,#ad1283 100%)"};
`;

class App extends React.Component {
  state = {
    cards: {},
    currentTheme: themes.theme1,
    popupShowing: null
  };

  componentDidMount() {
    const storedCities = localStorage.getItem("weatherAppCitiesAdded");
    const storedCitiesArr = storedCities ? storedCities.split(";") : [];

    storedCitiesArr.forEach(this.getWeather);
  }

  selectTheme = theme => {
    this.setState({
      currentTheme: theme
    })
  };

  parseWeatherForecast = (forecastList, offset) => {
    const validDates = [6, 7, 8, 12, 13, 14, 21, 22, 23, 0, 1, 2];

    let result = [];

    for (let i = 0, j = -1; i < forecastList.length; i++) {
      const newDate = new Date( (forecastList[i].dt + offset) * 1000).getHours();

      if ( newDate === 6 || newDate === 7 || newDate === 8) {
        j++;
        if (j === 4) break;

        result[j] = [];
      }

      if (validDates.some(e => e === newDate) && j >= 0) result[j].push(forecastList[i]);
    }

    result = result.map(element => {
      return element.map(e => {
        let date = new Date( (e.dt + offset) * 1000);
        return {
          temperature: Math.round(e.main.temp),
          pressure: Math.round(e.main.pressure * 0.75006375541921),
          humidity: e.main.humidity,
          windSpeed: e.wind.speed,
          windDirection: e.wind.deg,
          weather: e.weather[0].main,
          timeOfDay: timesOfDay[date.getHours()],
          date: date.toLocaleString('ru', dateOptions),
          day: days[date.getDay()]
        }
      });
    });

    return result;
  };

  submitHandler = async value => {
    let city;
    try {
      city = await getTranslatedText(value);

      if (this.state.cards[city]) return;

      await this.getWeather(city);
      this.storeCity(city);

    } catch(e) {
      console.log(e);
      this.showPopup('error');
    }
  };

  incrementStage = city => {
    if (this.state.cards[city].forecast.length === 0) this.getForecast(city);

    this.setState(prevState => {
      return {
        cards: {
          ...prevState.cards,
          [city]: {
            ...prevState.cards[city],
            stage: prevState.cards[city].stage + 1
          }
        }
      }
    });
  };

  decrementStage = city => {
    this.setState(prevState => {
      return {
        cards: {
          ...prevState.cards,
          [city]: {
            ...prevState.cards[city],
            stage: prevState.cards[city].stage - 1
          }
        }
      }
    })
  };

  removeCity = city => {
    let cards = {...this.state.cards};
    delete cards[city];

    this.setState({
      cards
    });

    this.removeStoredCity(city);
  };

  storeCity = city => {
    const currentCities = localStorage.getItem("weatherAppCitiesAdded") || "";
    const newItem = currentCities ? ";" + city : city;

    localStorage.setItem("weatherAppCitiesAdded", currentCities + newItem);
  };

  removeStoredCity = city => {
    const storedCities = localStorage.getItem("weatherAppCitiesAdded");
    const filteredCities = storedCities ? storedCities.split(';').filter(elem => elem !== city) : [];
    localStorage.setItem("weatherAppCitiesAdded", filteredCities.join(";"));
  };

  showPopup = type => {
    let popup;
    switch(type) {
      case 'error':
        popup = <ErrorPopup/>;
        break;
      case 'warning':
        popup = <WarningPopup/>;
        break;
      default:
        popup = null;
    }

    this.setState({
      popupShowing: popup
    });
    setTimeout(() => this.setState({popupShowing: null}), 3000);
  };

  getWeather = async city => {
    const weather = await getCurrentWeather(city);

    if (weather.cod !== 200 && weather.cod !== "200") throw new Error(`${weather.cod}, ${weather.message}`);

    let timeZoneOffset;
    try {
      timeZoneOffset = await getDifferenceWithCurrentZone(weather.coord.lat, weather.coord.lon);
    } catch(e) {
      console.log(e.message);
      this.showPopup('warning');
    }

    this.setState(prevState => {
      return {
        cards: {
          ...prevState.cards,
          [weather.name]: {
            stage: 1,
            cityName: `${weather.name}, ${weather.sys.country}` ,
            temperature: Math.round(weather.main.temp),
            pressure: Math.round(weather.main.pressure * 0.75006375541921),
            humidity: weather.main.humidity,
            windSpeed: weather.wind.speed,
            windDirection: weather.wind.deg,
            sunrise: (weather.sys.sunrise + timeZoneOffset) * 1000,
            sunset: (weather.sys.sunset + timeZoneOffset) * 1000,
            weather: weather.weather[0].main,
            timeZoneOffset: timeZoneOffset,
            forecast: []
          }
        }
      }
    });

    return true;
  };

  getForecast = async city => {
   let weather = await getForecastWeather(city);

   if (weather.cod !== "200" && weather.cod !== 200) return;

    this.setState(prevState => {
      return {
        cards:{
          ...prevState.cards,
          [city]: {
            ...prevState.cards[city],
            forecast: this.parseWeatherForecast(weather.list, prevState.cards[city].timeZoneOffset)
          }
        }
      }
    });
  };


  render() {
    return (
      <ThemeProvider theme={this.state.currentTheme}>
        <Container>
          {this.state.popupShowing}
          <GlobalStyles/>
          <WeatherWidget cards={this.state.cards}
                         themes={themes}
                         selectTheme={this.selectTheme}
                         add={this.submitHandler}
                         decrement={this.decrementStage}
                         increment={this.incrementStage}
                         remove={this.removeCity}/>
        </Container>
      </ThemeProvider>
    )
  }
}

export default App