import React from 'react'
import styled from 'styled-components'
import {string, number} from 'prop-types'

import TempDisplay from "./TempDisplay";
import WeatherInfo from "./WeatherInfo";

const PropTypes = {
  className: string,
  humidity: number.isRequired,
  windSpeed: number.isRequired,
  pressure: number.isRequired,
  temperature: number.isRequired,
  weather: string.isRequired,
  timeOfDay: string.isRequired
};

const Container = styled.div`
  display: flex;
  justify-content: space-between;
  box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.4);
  padding: 5px 10px;
  background: ${props => props.theme.info || "white"};
  
  @media (min-width: 900px) and (max-width: 1199px) {
    font-size: 12px;
  }
  
  @media (max-width: 599px) {
    font-size: 12px;
  }
`;

const RightField = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
`;

const Time = styled.p`
  align-self: flex-end;
  margin-bottom: 8px;
  
  @media (max-width: 599px) {
    margin-bottom: 0;
  }
`;

const StyledTempDisplay = styled(TempDisplay)`
  flex: 1;
`;

const TimeOfDayWeather = props => {
  const { humidity, windSpeed, pressure, temperature, weather, timeOfDay } = props;
  return (
    <Container className={props.className}>
      <WeatherInfo humidity={humidity}
                 windSpeed={windSpeed}
                 pressure={pressure} />
      <RightField>
        <Time>{timeOfDay}</Time>
        <StyledTempDisplay temperature={temperature} weather={weather}/>
      </RightField>
    </Container>
  );
};

TimeOfDayWeather.propTypes = PropTypes;

export default TimeOfDayWeather;