import React from 'react'
import styled, {css} from 'styled-components'
import {object, func, string} from 'prop-types'

import WeatherCardWithSideMenu from "./components/WeatherCardWithSideMenu/WeatherCardWithSideMenu";
import WidgetHeader from "./WidgetHeader";
import InputForm from "./components/InputForm";

const PropTypes = {
  className: string,
  cards: object,
  increment: func.isRequired,
  decrement: func.isRequired,
  remove: func.isRequired,
  add: func.isRequired
};

const DefaultProps = {
  cards: {},
};

const Widget = styled.section`
  position: relative;
  width: 95%;
  max-width: 1400px;
  border-radius: 5px;
  box-shadow: 0 0 5px 3px rgba(0, 0, 0, 0.4);
  display: grid;
  
  border: 2px solid ${props => props.theme.main || "#412198"};

  @media (max-width: 599px) {
    grid-template-rows: 100px minmax(400px, 1fr);
  }

  @media (min-width: 600px) and (max-width: 899px) {
    grid-template-rows: 180px minmax(720px, 1fr);
    width: 75%;
  }

  @media (min-width: 900px) {
    grid-template-rows: 140px minmax(560px, 1fr);
  }
`;

const WidgetBody = styled.div`
  position: relative;
  z-index: 5;
  border-radius: 0 0 2px 2px;
  box-shadow: inset 0 0 3px 3px rgba(0, 0, 0, 0.4);
  display: grid;

  background: ${props => props.theme.secondary || "#7349E5"};

  @media (max-width: 599px) {
    grid-auto-rows: 400px;
  }

  @media (min-width: 600px) and (max-width: 899px) {
    grid-auto-rows: 720px;
  }

  @media (min-width: 900px) {
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: 560px;
  }
`;

const WidgetInputField = styled.div`
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
  padding: 5px 10px;
  border-radius: 0 0 5px 5px;
  box-shadow: 0 0 3px 3px rgba(0, 0, 0, 0.4);
  display: flex;
  
  background: ${props => props.theme.main || "#412198"};
`;

const StyledWeatherCard = styled(WeatherCardWithSideMenu)`
  ${props => props.open ?
  css`
    @media (max-width: 899px) {
       grid-row-end: span 4;
       grid-template-rows: 1fr 3fr;
    }
     @media (min-width: 900px) {
       grid-template-columns: 1fr 3fr;
       grid-column-end: span 4;
     }` : ''}
`;

const WeatherWidget = ({ cards, increment, decrement, remove, add, className, themes, selectTheme }) => {
  const weatherCards = Object.keys(cards).map(city => {
    const card = cards[city];
    return (
      <StyledWeatherCard {...card}
                         key={card.cityName}
                         open={card.stage === 2}
                         toggle={card.stage === 1 ? () => increment(city) : () => decrement(city)}
                         remove={() => remove(city)}/>
    )
  });
  return (
    <Widget className={className}>
      <WidgetHeader themes={themes} selectTheme={selectTheme}/>
      <WidgetBody>
        <WidgetInputField>
          <InputForm text={"Add City"} onSubmit={add}/>
        </WidgetInputField>
        {weatherCards}
      </WidgetBody>
    </Widget>
  );
};

WeatherWidget.propTypes = PropTypes;
WeatherWidget.defaultProps = DefaultProps;

export default WeatherWidget;